import React from 'react';
import './Header.css';

const Header = ({
  topLine,
  headLine,
  description
}) => {
  return(
    <div className="about-wrapper">
      <div className="about-text-wrapper">
        <div className="about-heading">
          <h1>{topLine}</h1>
        </div>
        <div className="about-text">
          <h1>{headLine}</h1>
          <p>{description}</p>
        </div>
      </div>
    </div>
  );
}

export default Header;
