import React from 'react';
import './ContentsSection.css';

const ContentsSection = ({
  topLine,
  headLine, 
  description,  
  img, 
  alt, 
  imgStart,
  lastContent
}) => {
  return(
    <>
      <div className={
        lastContent ? 'home__contents-section-last': 'home__contents-section'} 
        >
        <div className="container">
          <div className="row home__contents-row" style={{display: 'flex', flexDirection: imgStart === 'start' ? 'row-reverse': 'row'}}>
            <div className="col">
              <div className="home__contents-text-wrapper">
                <div className="top-line">
                  {topLine}
                </div>
                <h1 className='heading'>
                  {headLine}
                </h1>
                <p className ='home__contents-subtitle'>
                  {description}
                </p>
              </div>
            </div>
            <div className="col">
              <div className="home__contents-img-wrapper">
                <img src={img} alt={alt} className="home__contents-img"/>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default ContentsSection;