import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import './App.css';
import ScrollToTop from './components/ScrollToTop';
import Navbar from './components/Navbar/Navbar';
import Footer from './components/Footer/Footer';
import Home from './pages/Home/Home';
import About from './pages/About/About';
import Cats from './pages/Cats';
import CatInfo from './pages/CatInfo/CatInfo';
import { catInfoObj1, catInfoObj2, catInfoObj3, catInfoObj4, catInfoObj5, catInfoObj6, catInfoObj7, catInfoObj8, catInfoObj9 } from './pages/CatInfo/Data';
import PricingPage from './pages/PricingPage/PricingPage';
import Access from './pages/Access/Access';
import Precautions from './pages/Precoutions/Precautions';
import Contact from './pages/Contact/Contact';

function App() {
  return (
    <Router>
      <ScrollToTop/>
      <Navbar/>
      <Switch>
        <Route path="/" exact component={Home}/>
        <Route path="/about" component={About}/>
        <Route path="/cats" component={Cats}/>
        <Route path="/lio" render={() => <CatInfo {...catInfoObj1}/>}/>
        <Route path="/blue" render={() => <CatInfo {...catInfoObj2}/>}/>
        <Route path="/chai" render={() => <CatInfo {...catInfoObj3}/>}/>
        <Route path="/clio" render={() => <CatInfo {...catInfoObj4}/>}/>
        <Route path="/kitty" render={() => <CatInfo {...catInfoObj5}/>}/>
        <Route path="/andalusia" render={() => <CatInfo {...catInfoObj6}/>}/>
        <Route path="/sue" render={() => <CatInfo {...catInfoObj7}/>}/>
        <Route path="/steve" render={() => <CatInfo {...catInfoObj8}/>}/>
        <Route path="/marine" render={() => <CatInfo {...catInfoObj9}/>}/>
        <Route path="/pricing" component={PricingPage}/>
        <Route path="/precautions" component={Precautions}/>
        <Route path="/access" component={Access}/>        
        <Route path="/contact" component={Contact}/>
      </Switch>
      <Footer/>
    </Router>
  );
};

export default App;
