import React from 'react';
import './Access.css';
import AccessInfo from '../../components/AccessInfo/AccessInfo';
import { Link } from 'react-router-dom';

const Access = () => {
  return (
    <div>
      <div className="access-wrapper">
        <div className="access-text-wrapper">
          <div className="access-heading">
            <h1>アクセス情報</h1>
          </div>
          <div className="access-text">
            <h3>Cat_Cafe</h3>
            <p>〒000-0000 〇〇県〇〇市〇〇区 〇丁目〇-〇 〇ビル〇F</p>
            <p>営業時間：10:00～19:00(最終入場:18:00)<br></br>
              メール：<Link to="/contact">XXXXXXXX@XXXX.XXX</Link><br></br>
              TEL：<Link to="/">000-0000-0000</Link>
            </p>
            <p>※駐車場はございません</p>
            <Link 
              to="/"
              target="_brank"
              className="parking-info">最寄りの駐車場はこちら</Link>
          </div>
        </div>
      </div>
      <AccessInfo/>
    </div>
  )
}

export default Access;
