import React from 'react';
import './PricingPage.css';
import Pricing from '../../components/Pricing/Pricing';
import { Link } from 'react-router-dom';
import { FiMail, FiPhone } from 'react-icons/fi';

const PricingPage = () => {
  return(
    <>
      <Pricing />
      <div className="pricing-cautions">
        <div className="pricing-cautions-text">
          <h3>※表示価格は全て税込価格となっております。</h3>
          <h4>原則予約優先でのご案内となります。</h4>
          <p>予約時に代表者名、日時、人数をご連絡ください。</p>
          <p>お問い合わせフォームに入力頂いたメールアドレスに当店から予約受付完了のご連絡をさせて頂きます。</p>
          <p>
            <FiMail className="contact-icons"/>お問い合わせは
            <Link to="/contact">こちら</Link><br></br>
            <FiPhone className="contact-icons"/>電話番号(<Link to="/">000-0000-0000</Link>)
          </p>
        </div>
      </div> 
    </>
  );
}

export default PricingPage;