import React from 'react';
import './Footer.css';
import { Button } from '../Button/Button';
import { Link } from 'react-router-dom';
import { GiCat } from 'react-icons/gi';
import { FaFacebookF, FaInstagram, FaTwitter, FaYoutube, FaLinkedin } from 'react-icons/fa';

const Footer = () => {
  return(
    <div className="footer-container">
      <section className="footer-contact">
        <p className="footer-contact-heading">
         Enjoy with CUTE CATS, DELICIOUS FOOD, HAPPY TIME!
        </p>
        <p className="footer-contact-text">
          You can contact us at any time.
        </p>
        <div className="btn-area">
          <Link to="/contact">
            <Button buttonStyle="btn--outline">お問い合わせ</Button>
          </Link>
        </div>
      </section>
      <div className="footer-links">
        <div className="footer-link-wrapper">
          <div className="footer-link-items">
            <h2>Pages</h2>
            <Link to="/">Top</Link>
            <Link to="/about">Cat_Cafeとは</Link>
            <Link to="/cats">猫スタッフ</Link>
            <Link to="/pricing">料金案内</Link>
            <Link to="/access">アクセス</Link>
            <Link to="/precautions">注意事項</Link>
          </div>
          <div className="footer-link-items">
            <h2>Social Media</h2>
            <Link 
              to="//www.instagram.com" 
              target="_blank"
            >Instagram</Link>
            <Link 
              to="//www.facebook.com" 
              target="_blank"
            >Facebook</Link>
            <Link 
              to="//www.youtube.com" 
              target="_blank"
            >Youtube</Link>
            <Link 
              to="//www.twitter.com" 
              target="_blank"
            >Twitter</Link>
            <Link 
              to="//www.linkedin.com" 
              target="_blank"
            >LinkedIn</Link>
          </div>
        </div>
      </div>
      <section className="social-media">
        <div className="social-media-wrap">
          <div className="footer-logo">
            <Link to="/" className="social-logo">
              <GiCat className="social-icon"/>
              Cat_Cafe 
            </Link>
          </div>
          <small className="website-rights">
            Cat_Cafe © 2021
          </small>
          <div className="social-icons">
            <Link 
              className="social-icon-link facebook"
              to="//www.facebook.com"
              target="_blank"
              aria-label="Facebook"
            >
              <FaFacebookF/>
            </Link>
            <Link 
              className="social-icon-link instagram"
              to="//www.instagram.com"
              target="_blank"
              aria-label="Instagram"
              >
              <FaInstagram/>
            </Link>
            <Link
              className='social-icon-link youtube'
              to='//www.youtube.com'
              target='_blank'
              aria-label='Youtube'
            >
              <FaYoutube />
            </Link>
            <Link
              className='social-icon-link twitter'
              to='//www.twitter.com'
              target='_blank'
              aria-label='Twitter'
            >
              <FaTwitter />
            </Link>
            <Link
              className='social-icon-link Linkedin'
              to='//www.linkedin.com'
              target='_blank'
              aria-label='LinkedIn'
            >
              <FaLinkedin />
            </Link>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Footer;