import React from 'react';
import './CatInfoSection.css';
import { GiFemale, GiMale } from 'react-icons/gi';
import { Link } from 'react-router-dom';
import { Button } from '../Button/Button';

const CatInfoSection = ({
  img,
  alt,
  headLine,
  gender,
  states1,
  states2,
  description
}) => {
  return(
    <div className='cat__contents-section'>
      <div className="container">
        <div className="row cat__contents-row">
          <div className="col">
            <div className="cat__contents-img-wrapper">
              <img src={img} alt={alt} className="cat__contents-img"/>
            </div>
          </div>
          <div className="col">
            <div className="cat__contents-text-wrapper">
              <h1 className='heading'>
                {headLine}
                <section className="cat__gender">
                  {gender === 'male' ? (<GiMale className="male"/>) : (<GiFemale className="female"/>)}
                </section>
              </h1>
              <p className='cat__states'>
                {states1}<br></br>{states2}
              </p>
              <p className='cat__contents-subtitle'>
                {description}
              </p>
            </div>
          </div>
        </div>
        <div className="cat__pictures-title">
          <h1>～ 写真集 ～</h1>
        </div>
        <div className="row cat__pictures-row">
          <div className="col">
            <div className="cat__contents-img-wrapper">
              <img src={img} alt={alt} className="cat__contents-img"/>
            </div>
          </div>
          <div className="col">
            <div className="cat__contents-img-wrapper">
              <img src={img} alt={alt} className="cat__contents-img"/>
            </div>
          </div>
          <div className="col">
            <div className="cat__contents-img-wrapper">
              <img src={img} alt={alt} className="cat__contents-img"/>
            </div>
          </div>
          <div className="col">
            <div className="cat__contents-img-wrapper">
              <img src={img} alt={alt} className="cat__contents-img"/>
            </div>
          </div>
        </div>
        <div className="cat__back-btn">
          <Link to="/cats">
            <Button 
              buttonStyle="btn--outline"
              buttonSize="btn--large" 
              buttonColor="blue"
            >一覧へ戻る</Button>
          </Link>
        </div>
      </div>
    </div>
  );
}

export default CatInfoSection;