// ↓hooksの読み込み
import { useState, useEffect } from 'react';

// ↓useForm関数の作成
// ↓引数にvalidate関数を受け取り、第2引数に渡される関数にcallbackを設定
const useForm = (validate, callback) => {
  // ↓定数valuesを定義し、初期値としてオブジェクト形式で各種valuesに空文字列を設定しておく
  const [values, setValues] = useState({
    username: '',
    email: '',
    contacttext: '',
  });
  
  // ↓定数errorsを定義し空のオブジェクトを代入
  const [errors, setErrors] = useState({});

  // ↓定数isSubmittingを定義(初期値にfalse)
  const [isSubmitting, setIsSubmitting] = useState(false);

  // ↓handleChange関数の作成
  // ↓eventオブジェクトでイベントが発火した部分を特定
  const handleChange = e => {
    // ↓e.targetでイベント発火した部分を取得してそれぞれのname値(username, email, password, password2),とそれに対応したvalue値を取得する
    const { name, value } = e.target;

    // ↓setValuesを呼び出し第1引数にvaluesを指定(スプレッド構文で新規オブジェクトを代入する既存オブジェクトを指定)
    // ↓第2引数でe.target.nameに入力されたvalues値(それぞれ取得したusername, email, password, password2)を代入
    setValues({
      ...values,
      [name]: value
    });
  };


  // ↓handleSubmit関数の作成
  // ↓eventオブジェクトを使用し、イベントが発火した部分を特定
  const handleSubmit = e => {
    // ↓イベント発生時の画面遷移防止
    e.preventDefault();

    // ↓渡ってくるvalidate関数にvaluesの値を渡してエラー判定する
    // ↓setErrors関数に渡し、返ってくる値をerrorsにセットする
    setErrors(validate(values));
    // ↓handleSubmit関数が実行されたらissubmittingの値をtrueにする
    setIsSubmitting(true);
  }

  // ↓useEffect()の作成
  // ↓第2引数にerrorsを配列として渡すことでerrorsに変更が合った場合とマウント時にuseEffectを実行する
  useEffect(() => {
    // ↓errorsオブジェクトが空でかつisSubmittingがtrueの場合にcallback関数で渡ってくるsubmitForm関数を呼び出しisSubmittedの値をtrueにする
    if(Object.keys(errors).length === 0 && isSubmitting){
      callback();
    }
  }, [errors]);


  // ↓handleChange関数, handleSubmit関数, values, errorsのreturn
  return { handleChange, handleSubmit, values, errors };

}

// ↓useFormのexport
export default useForm;