import React from 'react';
import './HeroSection.css';

const HeroSection = ({
  headLine,
  description,
  name
}) => {
  return(
    <div className="hero-container">
      <h1>{headLine}</h1>
      <p>{description}</p>
      <p className="name">{name}</p>
    </div>
  );
}

export default HeroSection;