import React from 'react';
import useForm from './useForm';
import validate from './validateInfo';
import '../../pages/Contact/Contact.css';
import { Link } from 'react-router-dom';
import { Button } from '../Button/Button';

const ContactForm = ({submitForm}) => {

  const { handleChange, values, handleSubmit, errors } = useForm(validate, submitForm);

  return (
    // ↓form要素を包括するdiv要素の作成
    <div className="form-content">
      {/* ↓form要素を作成 */}
      {/* ↓onSubmitイベントにhandleSubmit関数を適応してsubmitの状況管理を行う */}
      <form className="form" onSubmit={handleSubmit}>
        {/* ↓★usernameの入力欄を作成 */}
        {/* ↓input要素を包括するdiv要素の作成 */}
        <div className="form-inputs">
          {/* ↓label要素の作成 */}
          {/* ↓usernameのlabel */}
          <label htmlFor="username" className="form-label">
            代表者名
          </label>
            {/* ↓input要素の作成 */}
            {/* ↓nameにusernameを渡し識別できる様にする */}
            {/* ↓valueにuseFormのvaluesのusernameの値を適応(初期値は空文字) */}
            {/* ↓onChangeイベントにuseForm関数のhandleChange関数を設定 */}
            <input 
              id="username"
              type="text" 
              name="username" 
              className="form-input"
              placeholder="代表者名を入力ください"
              value={values.username}
              onChange={handleChange}
            />
            {/* ↓errorsの表示・非表示 */}
            {errors.username && <p>{errors.username}</p>}
        </div>
        {/* ↓★emailの入力欄の作成 */}
        <div className="form-inputs">
          {/* ↓label要素の作成 */}
          {/* ↓emailのlabel */}
          <label htmlFor="email" className="form-label">
            メールアドレス(Email)
          </label>
            {/* ↓input要素の作成 */}
            {/* ↓nameにemailを渡し識別できる様にする */}
            {/* ↓valueにuseFormのvaluesのemailの値を適応(初期値は空文字) */}
            {/* ↓onChangeイベントにuseFormのhandleChangeを設定 */}
            <input 
              id="email"
              type="email" 
              name="email" 
              className="form-input"
              placeholder="メールアドレスを入力ください"
              value={values.email}
              onChange={handleChange}
            />
            {/* ↓errorsの表示・非表示 */}
            {errors.email && <p>{errors.email}</p>}
        </div>
        {/* ↓★passwordの入力欄の作成 */}
        <div className="form-inputs">
          {/* ↓label要素の作成 */}
          {/* ↓passwordのlabel */}
          <label htmlFor="password" className="form-label">
            お問い合わせ内容
          </label>
            {/* ↓input要素の作成 */}
            {/* ↓name属性にpasswordを渡し識別できる様にする */}
            {/* ↓valueにuseFormのvaluesのpasswordの値を適応(初期値は空文字) */}
            {/* ↓onChangeイベントにuseFormのhandleChangeを設定 */}
            <textarea 
              id="contacttext"
              type="text" 
              name="contacttext" 
              className="form-textarea"
              placeholder="お問い合わせ内容を入力ください (予約時は人数、日時、希望コースをお知らせ下さい)"
              value={values.contacttext}
              onChange={handleChange}
            />
            {/* ↓errorsの表示・非表示 */}
            {errors.contacttext && <p>{errors.contacttext}</p>}
        </div>
        {/* ↓データ送信用のbuttonの作成 */}
        <div className="btn-wrapper">
          <Button 
            className="form-input-btn" 
            type="submit" 
            buttonStyle="btn--outline"
            buttonSize="btn--large" 
            buttonColor="blue"
          >送信する</Button>
        </div>
        <span className="form-input-login">
          ※事前に<Link to="/precautions">注意事項</Link>および<Link to="/pricing">料金案内</Link>の確認をお願いします。
        </span>
      </form>
    </div>
  );
};

export default ContactForm;
