import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { GiCat } from 'react-icons/gi';
import { FaBars, FaTimes } from 'react-icons/fa';
import { IconContext } from 'react-icons/lib';
import { Button } from '../Button/Button';
import './Navbar.css';

const Navbar = () => {

  const [ click, setClick ] = useState(false);
  const [ button, setButton ] = useState(true);
  const handleClick = () => {
    setClick(!click);
  }
  const closeMobileMenu = () => {
    setClick(false);
  }

  const showButton = () => {
    if(window.innerWidth <= 1122){
      setButton(false);
    } else {
      setButton(true);
    }
  }

  useEffect(()=>{
    showButton();
  }, []);

  window.addEventListener("resize", showButton);

  return (
    <>
      <IconContext.Provider value={{ color: '#fff' }}>
        <div className="navbar">
          <div className="navbar-container container">
            <Link to="/" className="navbar-logo">
              <GiCat className="navbar-icon"/>
              Cat_Cafe
            </Link>
            <div className="menu-icon" onClick={handleClick}>
              {click ?  <FaTimes/> : <FaBars/>}
            </div>
            <ul className={click ? "nav-menu active": "nav-menu"}>
              <li className="nav-item">
                <Link to="/" className="nav-links" onClick={closeMobileMenu}>
                  Home
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/about" className="nav-links" onClick={closeMobileMenu}>
                  Cat_Cafeとは
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/cats" className="nav-links" onClick={closeMobileMenu}>
                  猫スタッフ
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/pricing" className="nav-links" onClick={closeMobileMenu}>
                  料金案内
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/access" className="nav-links" onClick={closeMobileMenu}>
                  アクセス
                </Link>
              </li>
              <li className="nav-item">
                <Link to="/precautions" className="nav-links" onClick={closeMobileMenu}>
                  注意事項
                </Link>
              </li>
              <li className="nav-btn">
                {button ? 
                (
                  <Link to="/contact" className="btn-link">
                    <Button buttonStyle="btn--outline">お問い合わせ</Button>
                  </Link>
                ) : (
                  <Link to="/contact" className="btn-link" onClick={closeMobileMenu}>
                    <Button buttonStyle="btn--outline" buttonSize="btn--mobile">お問い合わせ</Button>
                  </Link>
                )}              
              </li>
            </ul>
          </div>
        </div>
      </IconContext.Provider>
    </>
  )
}

export default Navbar;
