import React from 'react';
import CardItem from '../CardItem';
import './Cards.css';

const Cards = () => {
  return(
    <div className="cards">
      <h1>Look in at our Cat_Cafe!</h1>
      <div className="cards__container">
        <div className="cards__wrapper">
          <ul className="cards__items">            
            <CardItem
              src="../../images/img-about.jpg"
              text="Cat_Cafeは人にとっても猫にとっても安らげる心地よい居場所の提供を目指しています。"
              label="Cat_Cafeのご紹介"
              path="/about"
            />            
            <CardItem
              src="../../images/img-cats.jpg"
              text="個性あふれる猫スタッフ達のご紹介。"
              label="猫スタッフのご紹介"
              path="/cats"
            />
          </ul>
          <ul className="cards__items">
            <CardItem
              src="../../images/img-pricing.jpg"
              text="Cat_Cafeのご利用料金について。"
              label="料金案内"
              path="/pricing"
            />
            <CardItem
              src="../../images/img-access.jpg"
              text="Cat_Cafeのアクセス情報。"
              label="アクセス"
              path="/access"
            />
            <CardItem
              src="../../images/img-precautions.jpg"
              text="利用前にお読みください。"
              label="注意事項"
              path="/precautions"
            />
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Cards;