import React from 'react';
import ContentsSection from '../../components/ContentsSection/ContentsSection';
import Header from '../../components/Header/Header';
import { precautionObjFour, precautionObjOne, precautionObjThree, precautionObjTwo, precoutionObjText, precautionHeaderObj } from './Data';
import './Precautions.css';

const Precautions = () => {
  return (
    <>
      <Header {...precautionHeaderObj}/>
      <ContentsSection {...precautionObjOne}/>
      <ContentsSection {...precautionObjTwo}/>
      <ContentsSection {...precautionObjThree}/>
      <ContentsSection {...precautionObjFour}/>
      {/* <HeroSection {...precoutionObjText}/> */}
      <div className="precaution-last-container">
        <h1>{precoutionObjText.description}</h1>
        <p className="name">{precoutionObjText.name}</p>
      </div>
    </>
  )
}

export default Precautions;
