import React from 'react';
import CardItem from './CardItem';

const CatsCards = () => {
  return(
    <div className="cards">
      <h1>We are waiting for you!</h1>
      <div className="cards__container">
        <div className="cards__wrapper">
          <ul className="cards__items">
            <CardItem
              src="../images/img-1.jpg"
              text="Cat_Cafeで一番の先輩猫"
              label="リオ(Lio)"
              path="/lio"
            />
            <CardItem
              src="../images/img-2.jpg"
              text="クールで賢い女の子"
              label="ブルー(Blue)"
              path="/blue"
            />
            <CardItem
              src="../images/img-3.jpg"
              text="人懐っこい甘えん坊"
              label="チャイ(Chai)"
              path="/chai"
            />
          </ul>
          <ul className="cards__items">
            <CardItem
              src="../images/img-4.jpg"
              text="優しいチャイのお兄さん"
              label="クリオ(Clio)"
              path="/clio"
            />
            <CardItem
              src="../images/img-5.jpg"
              text="好奇心旺盛でお転婆な女の子"
              label="キティ―(Kitty)"
              path="/kitty"
            />
            <CardItem
              src="../images/img-6.jpg"
              text="おっとりとした気品のあるお姉さん猫"
              label="アンダルシア(Andalusia)"
              path="/andalusia"
            />
          </ul>
          <ul className="cards__items">
            <CardItem
              src="../images/img-7.jpg"
              text="物静かで大人なスーさん"
              label="スー(Sue)"
              path="/sue"
            />
            <CardItem
              src="../images/img-8.jpg"
              text="いたずら好きなやんちゃっこ"
              label="スティーブ(Steve)"
              path="/steve"
            />
            <CardItem
              src="../images/img-9.jpg"
              text="高い所が大好きなツンデレさん"
              label="マリン(Marine)"
              path="/marine"
            />
          </ul>
        </div>
      </div>
    </div>
  );
}

export default CatsCards;