import React from 'react'
import { Link } from 'react-router-dom';
import './Pricing.css';
import { IconContext } from 'react-icons/lib';
import { SiGnuicecat } from 'react-icons/si';
import { GiCat, GiHollowCat } from 'react-icons/gi';

const Pricing = () => {
  return (
    <IconContext.Provider value={{ color: '#fff', size: 64}}> 
      <div className="pricing__section">
        <div className="pricing__wrapper">
          <h1 className="pricing__heading">料金案内</h1>
          <div className="pricing__container">
            <Link to="/contact" className="pricing__container-card">
              <div className="pricing__container-cardInfo">
                <div className="icon">
                  <GiCat />
                </div>
                <h4>¥1,100</h4>
                <h3>/ 1時間 (基本料金)</h3>
                <p>※ご利用１時間ごとに自動で加算</p>
                <br></br>
                <ul className="pricing__container-features">
                  <li>ドリンクバー付き</li>
                  <li>次回ご利用10%割引券付き</li>
                  <li>2名様以上のご予約で5％割引</li>
                  <li>小学生以下は¥900</li>
                </ul>
              </div>
            </Link>
            <Link to="/contact" className="pricing__container-card">
              <div className="pricing__container-cardInfo">
                <div className="icon">
                  <GiHollowCat />
                </div>
                <h4>¥2,800</h4>
                <h3>3時間 パック</h3>
                <p>※3時間経過以降、1時間毎に基本料金を追加</p>
                <ul className="pricing__container-features">
                  <li>ドリンクバー付き</li>
                  <li>猫のおやつ1品無料</li>
                  <li>次回ご利用10％割引券付き</li>
                  <li>2名様以上のご利用で5％割引</li>
                </ul>
              </div>
            </Link>
            <Link to="/contact" className="pricing__container-card">
              <div className="pricing__container-cardInfo">
                <div className="icon">
                  <SiGnuicecat />
                </div>
                <h4>¥5,300</h4>
                <h3>6時間 パック</h3>
                <p>※6時間経過以降、1時間毎に基本料金を追加</p>
                <ul className="pricing__container-features">
                  <li>ドリンクバー付き</li>
                  <li>猫のおやつ3品無料</li>
                  <li>次回ご利用15％割引券付き</li>
                  <li>2名様以上のご利用で10％割引</li>
                </ul>
              </div>
            </Link>
          </div>
        </div>
      </div>
    </IconContext.Provider>
  );
}

export default Pricing;
